.loader {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;

  &__element {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid gray; /* Blue */
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spin 1.8s linear infinite;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
